body {
	background-color: #dddddd;
	background-image: url("https://www.transparenttextures.com/patterns/absurdity.png");
	min-height: 70vh;
	padding: 1rem;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	color: hsl(198, 1%, 29%);
	font-family: "Shadows Into Light Two", cursive;
	text-align: center;
	font-size: 130%;
}

.container {
	width: 100%;
	height: auto;
	min-height: 500px;
	max-width: 500px;
	min-width: 250px;
	background: #f1f5f8;
	background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
	background-size: 25px 25px;
	border-radius: 20px;
	box-shadow: 4px 3px 7px 2px #00000040;
	padding: 1rem;
	box-sizing: border-box;
}
.heading {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 1rem;
}
.heading h1 {
	transform: rotate(2deg);
	padding: 0.2rem 1.2rem;
	border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
	background-color: #00917f;
	font-size: 1.5rem;
	color: #e8efeb;
}

.form input {
	box-sizing: border-box;
	background-color: transparent;
	padding: 0.7rem;
	border-bottom-right-radius: 15px 3px;
	border-bottom-left-radius: 0px 15px;
	border: solid 3px transparent;
	border-bottom: double 3px #ffb26b;
	font-family: "Shadows Into Light Two", cursive;
	font-size: 1rem;
	color: hsla(260, 2%, 25%, 0.7);
	width: 70%;
	margin-bottom: 20px;
}

button {
	padding: 0;
	border: none;
	font-family: "Shadows Into Light Two", cursive;
	text-decoration: none;
	padding-bottom: 3px;
	border-radius: 5px;
	background-color: #74c7b8;
}
button span {
	background: #f1f5f8;
	display: block;
	padding: 0.5rem 1rem;
	border-radius: 5px;
	border: 2px solid hsl(198, 1%, 29%);
}

li {
	text-align: left;
	position: relative;
	padding: 0.5rem;
}

footer {
	margin-top: 100px;
	font-family: Arial, Helvetica, sans-serif;

	color: #bfc0c1;
	font-size: 15px;
}
